import React from 'react';
import { Route, Switch } from 'react-router-dom';

import Login from './Login/Login';
import SignUp from './SignUp/SignUp';
import ForgotPassword from './ForgotPassword/ForgotPassword';
import TermsAndConditions from './SignUp/TermsAndConditions';

import BackgroundImage from '../../assets/img/home-background.png';

import './home.scss';

const Home = () => (
  <div className="no-auth-layout">
    <div
      className="no-auth-layout-banner"
      style={{ backgroundImage: `url(${BackgroundImage})` }}
    />

    <div className="no-auth-layout-sider">
      <div className="no-auth-layout-sider-children">
        <div className="no-auth-layout-sider-content">
          <Switch>
            <Route path="/login" component={Login} />
            <Route path="/sign-up" component={SignUp} />
            <Route path="/forgot-password" component={ForgotPassword} />
            <Route path="/terms-and-conditions" component={TermsAndConditions} />
          </Switch>
        </div>
      </div>
    </div>
  </div>
);

export default Home;
