import React, { useState, useEffect } from 'react';
import { Helmet } from 'react-helmet';
import { Formik, Form, Field } from 'formik';
import * as Yup from 'yup';
import { TextField } from 'formik-material-ui';
import { Link, useHistory } from 'react-router-dom';
import Button from '@material-ui/core/Button';
import CircularProgress from '@material-ui/core/CircularProgress';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import Alert from '@material-ui/lab/Alert';
import CssBaseline from '@material-ui/core/CssBaseline';
import { makeStyles } from '@material-ui/core/styles';

import { useDispatch, useSelector } from 'react-redux';
import { noAuthAxios } from '../../../utils/apiConfig';
import { login } from '../../../store/actions/authActions';

import Logo from '../../common/Logo/Logo';

const useStyles = makeStyles((theme) => ({
  forgotPassword: {
    marginBottom: 4,
    '& span, & a': {
      fontSize: 14,
    },
  },
  alert: {
    marginBottom: theme.spacing(4),
  },
  submit: {
    marginTop: theme.spacing(2),
  },
}));

const LoginSchema = Yup.object().shape({
  email: Yup.string().email('Invalid email').required('Required'),
  password: Yup.string().required('Required'),
});

const Login = () => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const history = useHistory();
  const auth = useSelector((state) => state.auth);
  const [error, setError] = useState('');

  useEffect(() => {
    if (auth.isAuthenticated) {
      history.push('/dashboard');
    }
  }, [auth.isAuthenticated]);

  const handleSubmit = (values, { setSubmitting, setFieldValue }) => {
    noAuthAxios
      .post('/login', values)
      .then((response) => {
        dispatch(login(response.data.token));
      })
      .catch((err) => {
        if (err.response) {
          const { data } = err.response;
          setError(data.error);
          setFieldValue('password', '', true);
        }
      })
      .then(() => {
        setSubmitting(false);
      });
  };

  return (
    <>
      <Helmet>
        <title>Login</title>
      </Helmet>

      <CssBaseline />
      <Box mb="7rem">
        <Logo />
      </Box>

      <div>
        <h3>Partners Portal</h3>

        <Formik
          initialValues={{
            email: 'eve.holt@reqres.i',
            password: 'cityslicka',
          }}
          validationSchema={LoginSchema}
          onSubmit={handleSubmit}
          render={({
            submitForm, isSubmitting, isValid, dirty,
          }) => (
            <Form>
              {error && (
              <Alert icon={false} severity="error" className={classes.alert}>
                {error}
              </Alert>
              )}

              <Field
                component={TextField}
                variant="outlined"
                fullWidth
                label="Email Address"
                name="email"
              />

              <Grid container className={classes.forgotPassword}>
                <Grid item xs={6}>
                  <span>Password</span>
                </Grid>
                <Grid container item xs={6} justify="flex-end">
                  <Link to="/forgot-password">Forgot Password?</Link>
                </Grid>
              </Grid>
              <Field
                component={TextField}
                type="password"
                variant="outlined"
                fullWidth
                name="password"
              />

              <Button
                size="large"
                fullWidth
                variant="contained"
                color="primary"
                type="submit"
                disabled={isSubmitting || !(isValid && dirty)}
                onClick={submitForm}
                className={classes.submit}
              >
                {isSubmitting && <CircularProgress size={20} />}
                Log In
              </Button>

              <Box mt={8}>
                {'Don’t have an account yet? '}
                <Link to="/sign-up">Sign up</Link>
              </Box>
            </Form>
          )}
        />
      </div>
    </>
  );
};

export default Login;
